@import url(https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC);
.App {
  padding: 1em;
}

label {
  display: inline-block;
  width: 100px;
}

header {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

/* typography */

h1 {
  text-align: center;
  font-family: "Playfair Display SC", serif;
  font-size: 64px;
}

html {
  font-family: "helvetica neue", sans-serif;
}

thead th {
  font-family: "Rock Salt";
  background-color: white;
}

th {
  letter-spacing: 2px;
  padding: 10px;
}

td {
  letter-spacing: 1px;
  overflow-x: hidden;
  overflow-y: hidden;
  font-size: 12px;
  padding: 10px;
  text-align: center;
}

img {
  object-fit: scale-down;
  height: 100%;
  width: 100%;
}

/* table */

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.content_td p {
  max-width: 100%;
  max-height: 100px;
  overflow-y: auto;
  text-overflow: ellipsis;
}

/* Alternating row background color*/

tr:nth-child(odd) {
  background-color: #fff0c7;
}

/* Column Size for thead */

thead #name,
#cuisine,
#photo,
#action {
  width: 10%;
}

thead #ingredients,
#prepartion {
  width: 30%;
}

/* Column size for tfoot */

tfoot #name,
#cuisine,
#photo,
#action {
  width: 10%;
}

tfoot #ingredients,
#preparation {
  width: 30%;
}

